.order {
    position: relative;
    padding-top: 110px;
    padding-bottom: 145px;


    &-left {
        height: 100%;
        display: flex;
        flex-direction: column;
        h2{
            font-size: 60px;
            line-height: 1.2 !important;
            margin-bottom: 4rem;
        }
        p{
            max-width: 80%;
        }
        .order-contacts{
            margin-top: auto;
            
            &-mail{
                color: var(--c-def);
            }
            &-tel{
                font-size: 1.5rem;
                color: var(--c-title);
            }
        }
    }

    &-contacts {
        display: flex;
        align-items: baseline;
        flex-direction: column;
    }
    &__btn{
        padding: 17px 72px;
        // border: none;
        outline: none;
        color: var(--c-white);
        font-weight: 700;
        width: fit-content;
        align-self: baseline;
    }
    .form {


        &__wrapper {

            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 1.5rem;

            .subm{
                grid-column: span 2;
                display: grid;
                grid-template-columns: auto 1fr;
                column-gap: 40px;
            }
            .file-display--container{
                grid-column: span 2;
                .file-display.small {
                    width: 100%;
                    padding: 0.375rem 0.75rem;
                    font-size: 0.5rem;
                    line-height: 1;
                    color: #495057;
                    background-color: #e9f5f9;
                }
            }
            .file{
                grid-column: span 2;
                display: grid;
                grid-template-columns: 1fr auto;
                span{
                    font-size: 14px;
                    color: #ccc;
                }
                .input.file-input{
                    display: none;
                }
                .order__upload{
                    display: flex;
                    gap: 0.5rem;
                    align-items: center;
                    svg{
                        width: 22px;
                        height: 21px;
                    }
                    label{
                        font-size: 14px;
                        color: var(--c-secondary);
                        text-transform: uppercase;
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                        gap: 0.5rem;
                    }
                    .button{
                        
                    }
                }
            }
            .in2{
                grid-column: span 2;
                textarea{
                    display: block;
                    width: 100%;
                    padding: 0.55rem 1rem 0.55rem 0;
                    font-size: 18px;
                    line-height: 1.3;
                    background-image: none;
                    border: none;
                    border-bottom: 2px solid #dadada;
                    border-radius: 0;
                    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
                    outline: none;
                    background-color: transparent;
                    border-color: var(--c-bg);
                    color: var(--c-secondary);
                }
            }
            .inputbox {
                position: relative;
                width: 196px;
                width: 100%;
            }

            .inputbox input {
                position: relative;
                width: 100%;
                padding: 20px 10px 10px;
                background: transparent;
                outline: none;
                box-shadow: none;
                border: none;
                // color: var(--c-white);
                color: var(--c-secondary);
                font-size: 1em;
                letter-spacing: 0.05em;
                transition: 0.5s;
                z-index: 10;
            }
            .inputbox input.def {
                position: relative;
                width: 100%;
                padding: 20px 10px 10px;
                background: transparent;
                outline: none;
                box-shadow: none;
                border: none;
                color: var(--c-def);
                font-size: 1em;
                letter-spacing: 0.05em;
                transition: 0.5s;
                z-index: 10;
            }
            .inputbox span {
                position: absolute;
                left: 0px;
                padding: 10px 10px 10px 0;
                font-size: 18px;
                // color: #8f8f8f;
                color: var(--c-grey);
                letter-spacing: 0.05em;
                transition: 0.5s;
                pointer-events: none;
            }

            .inputbox input:valid~span,
            .inputbox input:focus~span {
                color: var(--c-secondary);
                transform: translateX(-10px) translateY(-34px);
                font-size: 0.75em;
                padding: 20px 10px 10px 0;
            }

            .inputbox i {
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 2px;
                // background: var(--c-primary);
                background: var(--c-bg);
                border-radius: 4px;
                transition: 0.5s;
                pointer-events: none;
                z-index: 9;
            }

            .inputbox input:valid~i,
            .inputbox input:focus~i {
                height: 44px;
            }
        }
    }

    .dropdown{
        position: relative;
        width: 100%;
        height: 60px;
        // background: var(--c-primary);
        border-radius: 4px;
       
        i{
            height: 2px !important;
        }
        &::before{
            content: '';
            position: absolute;
            right: 20px;
            top: 20px;
            z-index: 2;
            width: 10px;
            height: 10px;
            // border: 2px solid var(--c-primary);
            border: 2px solid var(--c-grey);
            border-top: 2px solid var(--c-white);
            border-right: 2px solid var(--c-white);
            transform: rotate(-45deg);
            transition: 0.5s ;
            pointer-events: none;

        }
        input{
            background: var(--c-primary);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
            outline: none;
            border: none;
            padding: 12px;
            border-radius: 10px;
            color: var(--c-white);
            padding: 0 !important;
            font-size: 18px !important;
            &::placeholder{
                color: var(--c-def);
            }
        }
        .option{
            z-index: 10;
            position: absolute;
            top: 70px;
            width: 100%;
            background-color: #fff;
            border-radius: 10px;
            overflow: hidden;
            display: none;
            box-shadow: 0 30px 30px rgba(0,0,0,0.5);
            border-radius: 10px;
            div{
                padding: 12px 20px;
                cursor: pointer;
                &:hover{
                    background-color: #63baea;
                    color: var(--c-white);
                }
            }
           

        }
        &.active{
            &::before{
                top: 28px;
                transform: rotate(-225deg);
            }
            .option{
                display: block;
            }
            
            
        }
    }
    .sogl{
        font-size: 0.7rem;
        color: #ccc;
    }
    
}